// TODO: use CDN
import { STATIC_PREFIX } from '@/assets/js/consts/common'

export function parsedRtToHtml(delta) {
  const ops = delta.ops
  const blankPlaceholderCount = ops.filter(op => typeof op.insert === 'object' && 'blank-placeholder' in op.insert).length

  return ops.map((op, index) => {
    if (typeof op.insert === 'string') {

      // trim the last '\n'
      let insert = op.insert
      if (index + 1 === ops.length) {
        while (insert.endsWith('\n')) {
          insert = insert.replace(/\n+$/, '')
        }
      }
      if (insert.indexOf('\n') >= 0) {
        return insert.replace(/\n/g, '<br>')
      }
      let html = insert
      if (op.attributes) {
        if (op.attributes.underline) { html = `<u>${html}</u>` }
        if (op.attributes.italic) { html = `<em>${html}</em>` }
        if (op.attributes.bold) { html = `<strong>${html}</strong>` }
        if (op.attributes.link) { html = `<a href="${op.attributes.link}" target="_blank">${html}</a>` }
      }
      return html
    }
    if (typeof op.insert === 'object') {
      if ('image' in op.insert) {
        const style = []
        if (op.attributes) {
          if (op.attributes.width) style.push(`width:${op.attributes.width}px;`)
          if (op.attributes.height) style.push(`heightex:${op.attributes.height}px;`)
        }
        if (op.insert.image.startsWith('http')) {
          return `<div class="image"><img style="max-width:100%;${style.join(' ')}" src="${op.insert.image}"></div>`
        }
        return `<div class="image"><img style="max-width:100%;${style.join(' ')}" src="${STATIC_PREFIX}/${op.insert.image}"></div>`
      }
      if ('formula' in op.insert) {
        return String.raw`<span class="formula">\(${op.insert.formula}\)</span>`
      }
      if ('blank-placeholder' in op.insert) {
        if (blankPlaceholderCount > 1) {
          return `<span class="blank-placeholder-seq">${op.insert['blank-placeholder']}</span>`
        }
        return '<span class="blank-placeholder-seq"> </span>'
      }
    }
    return ''
  }).join('')
}

export function parsedRtToText(delta) {
  const ops = delta.ops
  return ops.map((op) => {
    if (typeof op.insert === 'string') {
      if (op.insert !== '\n') {
        return op.insert
      }
    }
    return ''
  }).join('')
}


export function checkRichtextEmpty(richtext) {
  if (!richtext.ops) return false
  return !(richtext.ops.some((op) => {
    if (op.insert) {
      if (typeof op.insert !== 'string') {
        return true
      }
      const content = op.insert.replace(/\s+/g, '')
      if (content) {
        return true
      }
    }
    return false
  }))
}


export function parseRichProperties(obj) {
  for (const property in obj) {
    if (property.endsWith('Rich')) {
      if (typeof obj[property] === 'string') {
        obj[property] = JSON.parse(obj[property])
      }
      if (Array.isArray(obj[property])) {
        obj[property] = { ops: obj[property] }
      }
      if (!('ops' in obj[property])) {
        obj[property] = { ops: [] }
      }

      Object.defineProperty(
        obj, property.replace('Rich', 'Html'),
        {
          get() { return parsedRtToHtml(obj[property]) },
        },
      )
      Object.defineProperty(
        obj, property.replace('Rich', 'Empty'),
        {
          get() { return checkRichtextEmpty(obj[property]) },
        },
      )
    }
  }
}


export function replacer(key, value) {
  if (key.endsWith('Rich')) {
    if (value.hasOwnProperty('ops')) {
      return JSON.stringify(value.ops)
    }
    return '[]'
  }
  return value
}



