<template>
  <div class="richtext-preview">
    <div class="ui form">
      <div class="field">
        <textarea v-model="textRichString" placeholder="输入富文本"></textarea>
      </div>
      <button class="ui primary button" @click="preview">转换</button>
    </div>
    <div class="preview-area" id="preview-area" v-html="textHtml">
    </div>
  </div>
</template>

<script>

import { parsedRtToHtml } from '@/question-bank/richtext'

export default {
  name: 'richtext-preview',
  props: {

  },
  data() {
    return {
      textRichString: '',
      textHtml: '',
    }
  },
  computed: {},
  methods: {
    preview() {
      let delta = []
      try {
        delta = JSON.parse(this.textRichString)
      } catch (error) {
        this.$toasted.error('JSON 解析出错，请输入正确的内容', {
          duration: 1000,
          position: 'bottom-center',
        })
      }
      this.textHtml = parsedRtToHtml({ ops: delta })
      this.$nextTick(() => {
        MathJax.Hub.Queue(['Typeset', MathJax.Hub])
        const placeholders = document.getElementsByClassName('blank-placeholder-label')
        Array.prototype.filter.call(placeholders, (el) => {
          el.style.width = '4rem'
          el.style.display = 'inline-block'
          el.style.margin = '0 .2em'
          el.style.lineHeight = '1.4'
          el.style.borderBottom = 'solid 2px #4a4a4a'
          el.style.backgroundColor = '#f9f9f9'
          el.style.textAlign = 'center'
        })
      })
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.richtext-preview {
  max-width: 800px;
  margin: 1rem auto;
  padding: 0 1rem;
}

.preview-area {
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  line-height: 1.8;
  font-size: 14px;
}

.blank-placeholder-label {
  width: 4rem;
  display: inline-block;
  margin: 0 .2em;
  line-height: 1.4;
  border-bottom: solid 2px #4a4a4a;
  background-color: #f9f9f9;
  text-align: center;
  color: red;
}



</style>



