import Vue from 'vue'
import VueRouter from 'vue-router'
import Toasted from 'vue-toasted'

import RichtextPreview from './richtext-preview'

require('./index.less')

Vue.config.productionTip = false

var axios = require('axios')

axios.defaults.headers.common['X_REQUESTED_WITH'] = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'
Vue.use(Toasted)
Vue.use(VueRouter)


const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/lab' : '/static/w/pages/lab.html',
  routes: [
    {
      path: '', component: RichtextPreview, name: 'richtext-preview', meta: { title: '富文本预览' },
    },
    {
      path: '/richtext-preview', component: RichtextPreview, name: 'richtext-preview', meta: { title: '富文本预览' },
    },
  ],
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  next()
})


new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  router,
  data: {},
})



